.catalog {
  padding: 0 calc(50% - 585px);
  padding-top: 89px;
  padding-bottom: 123px;
  background-color: #1b2d37;
  font-family: $roboto-condensed;
}

.catalog h2 {
  margin: 0;
  margin-bottom: 51px;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: $white;
}

.catalog__list {
  // display: flex;
  // justify-content: space-between;
  display: grid;
  grid-template-columns: 370px 370px 370px;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog__link {
  position: relative;
  display: block;
  // width: 370px;

  &::after {
    position: absolute;
    bottom: 5px;
    right: 0;
    width: 63px;
    height: 63px;
    background-color: $blue;
    background-image: url("../../img/arrow-right.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    content: "";
  }
}

.catalog__link img {
  width: 100%;
  opacity: 0.2;
}

.catalog__link span {
  position: absolute;
  top: 30px;
  left: 24px;
  display: block;
  width: 170px;
  padding: 20px;
  font-family: $roboto-condensed;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  color: $white;
}

@media (max-width: 1200px) {
  .catalog {
    padding: 0 calc(50% - 480px);
    padding-top: 89px;
  }

  .catalog__list {
    grid-template-columns: 300px 300px 300px;
  }
}

//tablet
@media (max-width: $tablet-width) {
  .catalog {
    padding: 0 calc(50% - 359px);
    padding-top: 48px;
    padding-bottom: 32px;
  }

  .catalog h2 {
    width: 660px;
    font-size: 32px;
    line-height: 45px;
  }

  .catalog__list {
    // flex-direction: column;
    display: block;
  }

  .catalog__link {
    width: 100%;
    margin-bottom: 41px;
  }

  .catalog__link img {
    height: 256px;
  }

  .catalog__link span {
    top: 21px;
  }
}

//mobile
@media (max-width: $mobile-width) {
  .catalog {
    padding: 0 calc(50% - 143px);
    padding-top: 49px;
    padding-bottom: 33px;
  }

  .catalog h2 {
    width: 100%;
    margin-left: 7px;
    margin-bottom: 44px;
    font-size: 28px;
    line-height: 39px;
    letter-spacing: -1px;
  }

  .catalog__link {
    margin-bottom: 25px;

    span {
      top: 0;
      left: 15px;
    }
  }
}
